<template>
<div class="radio-input-container w-100">
    <label class="text-input-label">{{label}}<span class="text-danger">{{isRequired ? ' *':''}}</span></label>
    <div class="radio-input-wrapper">
        <label v-for="(item, key) in options" :key="key" :class="`radio-option ${value === item.value ? 'selected' : ''}`">
            <input type="radio" :name="name" :checked="value == item.value" @change="localRadio" :value="item.value">
            {{item.name}}
        </label>
    </div>
    <div class="text-danger error" v-if="hasError">{{error}}</div>
</div>
</template>

<script>
export default {
    name: 'RadioInput',
    props: {
        options: Array,
        name: String,
        value: String,
        label: String,
        error: String,
        isRequired: {
            type: Boolean,
            default: false,
        },
        hasError: {
            type: Boolean,
            default: true,
        }
    },
    model: {
        event: `change`
    },
    methods: {
        localRadio: function (newValues) {
            this.$emit("change", newValues.currentTarget.value);
        }
    }
}
</script>

<style lang="scss">
.error {
    display: inline-block;
    font-size: 1.2rem;
    line-height: 2.0rem;
    width: 100%;
    margin-bottom: .3rem;
    min-height: 2.0rem;
    position: relative;
    display: none;
}
.text-input-label {
    font-family: NotoSansKR;
    font-weight: 300;
    font-size: 1.4rem;
    line-height: 2rem;
    color: #7C848D;
    margin-bottom: .7rem;
}
.radio-input-wrapper {
    display: flex;
    flex: 1;
    flex-flow: row;
    border-radius: 3.6rem;
    overflow: hidden;
    border: .05rem solid #e0e0e0;
    box-sizing: border-box;
    height: 3.6rem;
    .radio-option {
        display: inline-flex;
        flex: 1;
        align-items: center;
        justify-content: center;
        font-family: NotoSansKR;
        font-weight: 400;
        font-size: 1.5rem;
        line-height: 2.2rem;
        text-align: center;
        border-right: 0.05rem solid #E0E0E0;
        margin: 0;
        height: 100%;

        input[type="radio"] {
            width: 0;
            opacity: 0;
        }

        &.selected {
            background-color: rgba(0, 111, 227, 0.1);
            color: rgb(0, 111, 227);
        }
        &:last-child {
            border-right: 0;
        }
    }
}
</style>
