<template>
    <div class="button-wrapper" :class="`${wrapperClass || ''} ${isDisabled ? 'disabled' : ''}`">
        <button :class="`btn btn-${primary ? 'primary' : this.default ? 'default' : 'primary'} cy-btn`">{{label}}</button>
    </div>
</template>
<script>
export default {
    name: 'Button',
    props: {
        primary: {
            type: Boolean,
            default: false,
        },
        default: {
            type: Boolean,
            default: false,
        },
        label: {
            type: String,
        },
        wrapperClass: String,
        isDisabled: {
            type: Boolean,
            default: false,
        }
    }
}
</script>
<style lang="scss" scoped>
.button-wrapper {
    display: block;
    width:100%;
    height: 5.0rem;
    font-family: NotoSansKR;
    font-weight: 500;
    font-size: 1.7rem;
    line-height: 2.5rem;
    position: relative;
    &.disabled:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(255,255,255, 0.5);
        border-radius: .3rem;
    }
    .cy-btn {
        height: 100%;
        width: 100%;
        border-radius: .4rem;
        font-family: inherit;
        font-weight: inherit;
        font-size: inherit;
        line-height: inherit;
        text-align: center;
        &.btn-primary {
            background: #006FE3;
            color: #FFFFFF;
        }
        &.btn-default {
            color: #006FE3;
            border: .1rem solid #006FE3;
        }
        &:focus {
            outline: 0;
            box-shadow: none;
        }
    }
}
</style>