<template>
<div class="container-fluid language-skills-wrapper">
    <div>
        <div class="row m-0 language-skills-row-content">
            <RadioInput isRequired :label="$t('LanguageSkills.KOREAN')" v-model="FormData.korean" :options="[
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.HIGH}`), value: $CYconstants.LANGUAGE_SKILLS.HIGH},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.MIDDLE}`), value: $CYconstants.LANGUAGE_SKILLS.MIDDLE},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.LOW}`), value: $CYconstants.LANGUAGE_SKILLS.LOW},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.NONE}`), value: $CYconstants.LANGUAGE_SKILLS.NONE},
                    ]" :name="'korean'" :hasError="false"></RadioInput>
        </div>
        <div class="row m-0 language-skills-row-content">
            <RadioInput isRequired :label="$t('LanguageSkills.ENGLISH')" v-model="FormData.english" :options="[
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.HIGH}`), value: $CYconstants.LANGUAGE_SKILLS.HIGH},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.MIDDLE}`), value: $CYconstants.LANGUAGE_SKILLS.MIDDLE},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.LOW}`), value: $CYconstants.LANGUAGE_SKILLS.LOW},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.NONE}`), value: $CYconstants.LANGUAGE_SKILLS.NONE},
                    ]" :name="'english'" :hasError="false"></RadioInput>
        </div>
        <div class="row m-0 language-skills-row-content">
            <RadioInput isRequired :label="$t('LanguageSkills.CHINESE')" v-model="FormData.chinese" :options="[
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.HIGH}`), value: $CYconstants.LANGUAGE_SKILLS.HIGH},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.MIDDLE}`), value: $CYconstants.LANGUAGE_SKILLS.MIDDLE},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.LOW}`), value: $CYconstants.LANGUAGE_SKILLS.LOW},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.NONE}`), value: $CYconstants.LANGUAGE_SKILLS.NONE},
                    ]" :name="'chinese'" :hasError="false"></RadioInput>
        </div>
        <div class="row m-0 language-skills-row-content">
            <RadioInput isRequired :label="$t('LanguageSkills.JAPANESE')" v-model="FormData.japanese" :options="[
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.HIGH}`), value: $CYconstants.LANGUAGE_SKILLS.HIGH},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.MIDDLE}`), value: $CYconstants.LANGUAGE_SKILLS.MIDDLE},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.LOW}`), value: $CYconstants.LANGUAGE_SKILLS.LOW},
                        {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.NONE}`), value: $CYconstants.LANGUAGE_SKILLS.NONE},
                    ]" :name="'japanese'" :hasError="false"></RadioInput>
        </div>
    </div>
    <div class="row m-0 language-skills-row-content">
        <Button :label="$t('LanguageSkills.SAVE')" :wrapperClass="'w-100'" @click.native="saveLanguageSkill"></Button>
    </div>
</div>
</template>

<script>
import RadioInput from '@/components/Common/Input/RadioInput'
import Button from '@/components/Common/Button/Button'
import {
    SHOW_POPUP_BUTTON
} from '@/mutations/Common.mutation'
import {
    CHANGE_LANGUAGE_SKILL
} from '@/mutations/Auth.mutation'

export default {
    name: "LanguageSkills",
    components: {
        RadioInput,
        Button,
    },
    data: function () {
        return {
            FormData: {
                korean: this.$CYconstants.LANGUAGE_SKILLS.HIGH,
                english: this.$CYconstants.LANGUAGE_SKILLS.HIGH,
                chinese: this.$CYconstants.LANGUAGE_SKILLS.HIGH,
                japanese: this.$CYconstants.LANGUAGE_SKILLS.HIGH,
            },
            user: JSON.parse(localStorage.getItem('user'))
        }
    },
    computed: {
        skills() {
            return this.$store.state.MyPage.myCareer;
        },
    },
    async created() {
        await this.setFormData();
    },
    methods: {
        async saveLanguageSkill() {
            try {
                let params = {
                    langEn: this.FormData.english,
                    langCn: this.FormData.chinese,
                    langKr: this.FormData.korean,
                    langJp: this.FormData.japanese
                }

                if (this.user) {
                    params['userId'] = this.user.userId
                }
                await this.$store.dispatch("CHANGE_LANGUAGE_SKILL", params);
                this.$store.dispatch(SHOW_POPUP_BUTTON, this.$t('LanguageSkills.UPDATE_SUCCESS'));
            } catch (error) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, error.message);
            }
        },
        setFormData() {
            if (this.skills) {
                this.FormData.chinese = this.skills.langCn ? this.skills.langCn : this.$CYconstants.LANGUAGE_SKILLS.NONE
                this.FormData.english = this.skills.langEn ? this.skills.langEn : this.$CYconstants.LANGUAGE_SKILLS.NONE
                this.FormData.korean = this.skills.langKr ? this.skills.langKr : this.$CYconstants.LANGUAGE_SKILLS.NONE
                this.FormData.japanese = this.skills.langJp ? this.skills.langJp : this.$CYconstants.LANGUAGE_SKILLS.NONE
            }
        }
    }
}
</script>
